<template>
  <div v-if="tabArray.length > 0" class="tabs-area">
    <ul class="tabs">
      <li v-for="(tab, index) in tabArray" :data-ec="tab" :key="index" :class="['tab', { 'tab--active': state.currentTab === index }]" @tap.prevent="onTapTab(index)">
        <span class="tab__text">
          {{ tab }}
        </span>
      </li>
    </ul>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from 'vue'

interface State {
  currentTab: number
}

export default defineComponent({
  props: {
    tabArray: {
      type: Array,
      required: true,
    },
  },
  setup(props, { emit }) {
    const state = reactive<State>({
      currentTab: 0,
    })

    const onTapTab = (e: number): void => {
      state.currentTab = e
      emit('tap-tab', e)
    }

    return {
      state,
      onTapTab,
    }
  },
})
</script>

<style lang="scss" scoped>
@import '~@/assets/css/colors';

.tabs-area {
  display: flex;
  white-space: nowrap;
  background: #f2f2f2;
