}

.tabs {
  display: flex;
  justify-content: center;
  align-items: center;
}

.tab {
  width: 128px;
  padding: 11px 12px 4px;
  border-radius: 15px 15px 0px 0px;
  background: #e6e6e6;
  text-align: center;
  line-height: 1;

  &-- {
    &active {
      background: $main;
      color: #fff;
    }

    &active .tab__text {
      color: inherit;
    }
  }

  &__ {
    &text {
      pointer-events: none;
      font-size: 1.2rem;
      font-weight: 600;
      color: $gray1;
    }
  }
}
</style>
