import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import SentEmailDetail from '@/components/container/Top/SentEmailDetail.vue'
import SentEmailTop from '@/components/container/Top/index.vue'
import CallTop from '@/components/container/Call/Top.vue'
import CallDetail from '@/components/container/Call/CalllDetail.vue'


const routes: Array<RouteRecordRaw> = [
  // {
  //   path: '/',
  //   name: 'home',
  //   component: HomeView
  // },
{
    path: '/',
    name: 'home',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/MainPage.vue')
  },

  {
    path: '/CallTop',
    name: 'CallTop',
    component: CallTop
  },

  {
    path: '/CallDetail',
    name: 'CallDetail',
     component: CallDetail,
    props: (route) => ({
       id: String(route.query.id),
      }),
  },

  {
    path: '/SentEmailTop',
    name: 'SentEmailTop',
    component: SentEmailTop
  },

   {
    path: '/s',
    name: 's',
     component: SentEmailDetail,
    props: (route) => ({
        imgUrl: String(route.query.imgUrl),
        title: String(route.query.title),
        text: String(route.query.text),
      }),
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
