<template>
  <div class="border" :style="style"></div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    color: {
      type: String,
      required: false,
      default: '#c0c0c0',
    },
  },
  setup(props) {
    const style = {
      '--color': props.color,
    }

    return {
      style,
    }
  },
})
</script>

<style scoped lang="scss">
@import '~@/assets/css/colors';

.border {
  height: 100%;
  width: 1px;
  background-color: var(--color);
}
</style>
