                </div>
                 <div @tap="onTapClearButton2(index)" >
                  {{ obj.MR }}
                </div>

                <template v-for="(obj2, index2) in state.monthArry" :key="index2">
                 <div :class="getSpareClass(obj.data[obj2])" style="margin-right: 20px;"> <span v-if="obj.data[obj2]">{{ obj.data[obj2] }}</span><span v-else>0</span></div>
