<script lang="ts">
import {
  defineComponent,
  ref,
  computed,
  reactive,
  onMounted,
  onUpdated,
} from "vue";
import IScroll from "iscroll";

import { SelectBox } from "@/components/presentational/organisms";
import dayjs from "dayjs";
import { sleep } from "@/utils/sleep";

import { useApplicationStore } from '@/store/modules/applicationModule'
import { useAccountStore } from '@/store/modules/accountModule'



interface State {
  iScrollObj: null | IScroll;
  activityObj: any;
  testObj: any;
  selectedFilterItems: {
    channel?: string;
    lecture?: string;
    MR?: any;
    MR2?: string;
    myProduct?: string;
    hospital?: string;
    hospital2?: string;
    startDate: string;
    endDate: string;
  };
  keyword: any;
  accountList: any;
  accountList2: any;
  data: any;
  data2: any;
  data3: any;
  options: any;
  loaded: boolean;
  plugins: any;
  h: any;
  emailList: any;
  girdArry: any;
  sentEmailListDetail: any;
  style: any;
  monthArry: any;
  sumArry: any;
  maxNumber: any;
}

export default defineComponent({
  components: {
    SelectBox,

  },
  props:{
		id: {
      type: String,
      required: true,
    },
	},
  setup(props) {
    const root = ref(null);
    const chart = ref(null);
    const ApplicationStore = useApplicationStore()
    const Account = useAccountStore()
    const state = reactive<State>({
      iScrollObj: null,
      activityObj: null,
      keyword: "",
      testObj: {
        channel: {
          name: "チャネル",
          list: {
            すべて: "すべて",
            講演会: "講演会",
            "One to One": "One to One",
            Webサイト: "Webサイト",
            コール: "コール",
            スタンプ: "スタンプ",
            参加ログ: "参加ログ",
          },
          // list: ['すべて', 'ディテールコール', 'メール', 'CSLC', 'イベント', '3rd Party'],
        },
        lecture: {
          name: "講演会名",
          list: {
            すべて: "すべて",
          },
        },
        MR: {
          name: "MR",
          list: {
            すべて: "すべて",
          },
        },
        hospital: {
          name: "医師名",
          list: {
            すべて: "すべて",
          },
        },
      },
      selectedFilterItems: {
        channel: null,
        lecture: null,
        MR: ["すべて"],
        MR2: null,
        myProduct: null,
        hospital: null,
        hospital2: null,
        startDate: "",
        endDate: "",
      },
      accountList: null,
      accountList2: [],
      data: null,
      data2: null,
      data3: {},
      sentEmailListDetail:null,
      options: null,
      loaded: false,
      plugins: null,
      h: null,
      emailList: {},
      girdArry: [],
      style: null,
      monthArry: null,
      sumArry: {},
      maxNumber:null
    });

    console.log('ae');
    console.log(props);
    
    
    const shouldShowHelpPopup = computed(() => ApplicationStore.getShouldShowHelpPopup)


 
   

    onMounted(async () => {
      state.iScrollObj = new IScroll(
        root.value.querySelector(".iscroll-wrapper"),
        {
          scrollX: false,
          scrollY: true,
          disableTouch: true,
          disablePointer: false,
          useTransition: false,
          scrollbars: "custom",
          fadeScrollbars: true,
          mouseWheel: true,
        }
      );

      
     let callList2 = Account.getsentCallListByKey(props.id)

      if (!callList2) {
        await Account.JSON__CALL_DATA()
         callList2 = Account.getsentCallListByKey(props.id)
      }


    state.data = computed(() => {
      console.log('test');
    
      

      const result = []
     

      const callList = { ...callList2 }

      console.log(callList);

      for (const key in callList) {


        for (const key2 in callList[key]) {

          for (const key3 in callList[key][key2]) {
             const obj = {}

            for (const element in callList[key][key2][key3]) {
             
              obj[element] = callList[key][key2][key3][element].length
            }

            const rec = {
            営業部:props.id,
            エリア: key,
            テリトリー名: key2,
            MR: key3,
            data:obj
          }
          result.push(rec)

          }


          
         
        }
        

      }
      
      
      

      return result.sort((a, b) => {
        if (a.エリア > b.エリア) return 1
        if (a.エリア < b.エリア) return -1
        if (a.テリトリー名 > b.テリトリー名) return 1
        if (a.テリトリー名 < b.テリトリー名) return -1

          })
    })



      const monthData = state.data.map((p) => Object.keys(p["data"]));

      const result2 = []

      for (const element of monthData) {
        for (const element2 of element) {
          if (!result2.includes(element2)) {
            result2.push(element2)
          }
          
        }
        
      }

      const monthArryReserve = result2.sort((a, b) => {
          if (dayjs(a) < dayjs(b)) return 1
          if (dayjs(a) > dayjs(b)) return -1
      }).slice(0,6)


      state.monthArry = monthArryReserve.sort((a, b) => {
          if (dayjs(a) > dayjs(b)) return 1
          if (dayjs(a) < dayjs(b)) return -1
      })
      

      const maxIndexArry = []
       
      for (const element of state.monthArry) {
        const monthSum = state.data.map((p) => p["data"][element]).filter(v => v);
        
        const sum = monthSum.reduce((sum, num) => sum + num, 0);
        const maxIndex = Math.max(...monthSum);
        maxIndexArry.push(maxIndex)
        state.sumArry[element] = sum
      }

      state.maxNumber = Math.max(...maxIndexArry)



      const mrList = state.data.map((p) => p["MR"]).sort((a, b) => {
          if (a > b) return 1
          if (a < b) return -1
      })

      console.log(mrList);
      

      for (const key of mrList) {
        if (!state.testObj.MR.list[key]) {
          state.testObj.MR.list[key] = key
          state.selectedFilterItems.MR.push(key)
        }
      }
      
      
      

    
    });

    onUpdated(() => {

      


      setTimeout(() => {
        if (state.iScrollObj) {
          state.iScrollObj.refresh();
        }
      }, 500);
    });

    const test = (selectValue) => {
      console.log(selectValue);
    };

    const onSelectedStartDate = (selectValue) => {
      state.selectedFilterItems.startDate = dayjs(selectValue)
        .startOf("day")
        .toISOString();
    };

    const onSelectedEndDate = (selectValue) => {
      state.selectedFilterItems.endDate = dayjs(selectValue)
        .startOf("day")
        .toISOString();
    };

    const getShareName = (_str) => {
      if (!_str) return null;
      if (_str.includes("Face_to_face_vod")) {
        return "面会";
      } else if (_str.includes("Video_vod")) {
        return "Web面談";
      } else if (_str.includes("Phone_vod")) {
        return "電話";
      } else if (_str.includes("Email_vod")) {
        return "メール";
      } else if (_str.includes("Other_vod")) {
        return "その他";
      } else {
        return _str;
      }
    };

    const onTapSelectBoxItem = (_obj) => {
      state.selectedFilterItems[_obj.category] =
        _obj.selectedValue !== "すべて" ? _obj.selectedValue : null;
      state.selectedFilterItems[`${_obj.category}2`] =
        _obj.selectedValue2 !== "すべて" ? _obj.selectedValue2 : null;
    };

    const onTapClearButton = () => {
      state.keyword = "";
    };

    const onTapClearButton2 = (_obj) => {
      console.log(_obj);

    };

    const onTapOutside = async (evt) => {
      await sleep(100); // onTapFilterButton の同時発火でパネルがチラつかないよう対策

      const item = document.getElementById("search") as HTMLInputElement;
      if (item) {
        item.blur();
      }
    };

    const getSpareClass = (spare1) => {

      const targetNumber = spare1 / state.maxNumber * 100

      if (targetNumber >= 80) {
        return 'spare1'
      } else if (targetNumber >= 45) {
        return 'spare2'
      } else if (targetNumber >= 30) {
        return 'spare3'
      } else {
        return
      }
    }



    return {
      state,
      root,
      chart,
      dayjs,
      shouldShowHelpPopup,
      onTapSelectBoxItem,
      onTapClearButton,
      onTapClearButton2,
      onSelectedStartDate,
      onSelectedEndDate,
      getShareName,
      onTapOutside,
      getSpareClass,
    };
  },
});
</script>

