<template>
  <div v-if="tabArray.length > 0" :style="style" class="tabs-area">
    <ul class="tabs">
      <li
        v-for="(tabItem, index) in tabArray"
        :data-ec="`${category ? category : tabItem.title}`"
        :key="index"
        :class="['tab', { 'tab--active': state.currentTab === index }]"
        @tap.prevent="onTapTab(tabItem.id, index)"
      >
        <span class="tab__text"> {{ tabItem.title }}<Badge v-if="tabItem.isShowBadge" /></span>
      </li>
    </ul>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, PropType, reactive, watch } from 'vue'
import { Badge } from '@/components/presentational/atoms/index'

interface State {
  currentTab: number
}

interface TabItem {
  id: string | number
  title: string
  isShowBadge: boolean
}

export default defineComponent({
  components: {
    Badge,
  },
  props: {
    tabArray: {
      type: Array as PropType<Array<TabItem>>,
      required: true,
    },
    category: {
      type: String,
      required: true,
    },
    color: {
      type: String,
      required: false,
      default: '#333',
    },
    resetValue: {
      type: Number,
      required: false,
      default: null,
    },
    currentId: {
      type: String,
      required: false,
    },
  },
  setup(props, { emit }) {
    const state = reactive<State>({
      currentTab: null,
    })

    watch(
      () => props.currentId,
      () => {
        (props.tabArray as Array<TabItem>).forEach((item, index) => {
          if (props.currentId === item.id) {
            state.currentTab = index
          }
        })
      }
    )

    onMounted(() => {
      reset()
    })

    const reset = () => {
      props.resetValue !== null ? (state.currentTab = props.resetValue as number) : (state.currentTab = null)
    }

    const onTapTab = (id: string | number, index: number): void => {
      state.currentTab = index
      emit('tap-tab', id)
    }

    const style = {
      '--color': props.color,
    }

    return {
      state,
      reset,
      onTapTab,
      style,
    }
  },
})
</script>

<style lang="scss" scoped>
@import '~@/assets/css/colors';

.tabs-area {
  white-space: nowrap;
}

.tabs {
  display: flex;
  justify-content: center;
  align-items: center;
}

.tab {
  padding: 4px 12px;
  border-bottom: 3px solid transparent;

  &-- {
    &active {
      border-bottom: 3px solid var(--color);
    }

    &active .tab__text {
      color: inherit;
    }
  }

  &__ {
    &text {
      pointer-events: none;
      display: inline-flex;
      font-size: 1.4rem;
      color: $gray1;
    }
  }
}
</style>
