<template>
  <div :data-ec="category" @tap.prevent="onTapCard(id)" class="card">
    <span class="card__text fwb">{{ accountName }}</span>
    <span class="card__text">{{ facilityName }}</span>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    id: {
      type: String,
      required: true,
    },
    category: {
      type: String,
      required: true,
    },
    accountName: {
      type: String,
      required: true,
    },
    facilityName: {
      type: String,
      required: true,
    },
  },
  setup(props, { emit }) {
    const onTapCard = (id) => {
      emit('tap-card', id)
    }

    return { onTapCard }
  },
})
</script>
