<template>
  <button @tap.prevent="tap" :style="style" class="button" type="submit">?</button>
</template>

<script lang="ts">
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    tap: {
      type: Function,
      required: true,
    },
    color: {
      type: String,
      required: false,
      default: '#333',
    },
  },
  setup(props) {
    const style = {
      '--color': props.color,
    }

    return { style }
  },
})
</script>
