<template>
  <div ref="root" class="select" :style="style" @tap="changeSelectBoxDisplay">
    <div class="select__value">{{ selectObj[category].name }}：{{ selectedValue || 'すべて' }}</div>
    <div :class="['select__arrow', { 'select__arrow--on': state.isShowSelectBox }]"></div>
    <transition name="fade">
      <div class="select__items" v-if="state.isShowSelectBox" v-click-outside="onTapOutside">
        <div class="iscroll-wrapper">
          <div class="iscroll__scroller">
            <transition-group tag="div">
              <template v-for="(key, value) in selectObj[category].list" :key="value">
                <div :class="['select__item', { 'select__item--on': isSelected(value) }]" :data-key="key" @tap.prevent="onTapItem">
                  <span class="select__item-text" :data-key="key">{{ value }}</span>
                </div>
              </template>
            </transition-group>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive, onMounted, onUpdated, computed } from 'vue'
import IScroll from 'iscroll'
import vClickOutside from 'click-outside-vue3'
import { sleep } from '@/utils/sleep'

interface State {
  iScrollObj: null | IScroll
  isShowSelectBox: boolean
  selectedValueArry:any
}

export default defineComponent({
  directives: {
    clickOutside: vClickOutside.directive,
  },
  props: {
    selectObj: {
      type: Object,
      required: true,
    },
    category: {
      type: String,
      required: true,
    },
    selectedValue: {
      type: Array,
      required: false,
      default: null,
    },
    selectedValue2: {
      type: String,
      required: false,
      default: null,
    },
    width: {
      type: String,
      required: false,
      default: '135px',
    },
  },
  setup(props, { emit }) {
    const root = ref(null)
    const state = reactive<State>({
      iScrollObj: null,
      isShowSelectBox: false,
      selectedValueArry:null
    })

    const onTapItem = (e): void => {
      console.log(e.target)


      if (e.target.textContent === "すべて") {
        if (props.selectedValue.includes("すべて")) {
          e.stopPropagation()
          emit('tapItem', {
            category: props.category,
            selectedValue: [],
          })
        } else {
          e.stopPropagation()
          emit('tapItem', {
            category: props.category,
            selectedValue: Object.keys(props.selectObj[props.category].list),
          })
        }

      } else {

        if (props.selectedValue.includes(e.target.textContent)) {

           const copyObj = [...props.selectedValue]




         let newArray = copyObj.filter(n => n !== e.target.textContent);



          if (props.selectedValue.includes("すべて")) {
            newArray = newArray.filter(n => n !== "すべて");
            
          }

          e.stopPropagation()
          emit('tapItem', {
            category: props.category,
            selectedValue: newArray,
          })


        } else {


          const copyObj = [...props.selectedValue]

          copyObj.push(e.target.textContent)

          if (!props.selectedValue.includes("すべて")) {
            const num = Object.keys(props.selectObj[props.category].list).length - copyObj.length 

          if (num === 1) {
            copyObj.push("すべて")
          }

            
          }

         
          e.stopPropagation()
          emit('tapItem', {
            category: props.category,
            selectedValue: copyObj,
          })
        }



        // state.isShowSelectBox = false
      }

    }

    const changeSelectBoxDisplay = () => {
      console.log(props.selectedValue)
      if (state.isShowSelectBox) return
      state.isShowSelectBox = true
    }

    const onTapOutside = async () => {
      console.log('onTapOutside')
      await sleep(100) // onTapFilterButton の同時発火でパネルがチラつかないよう対策
      state.isShowSelectBox = false
    }

    const isSelected = (_value) => {


     if (props.selectedValue.includes(_value)) {
        return true
      } else {
        return false
      }


      // console.log(props.selectedValue ? _value === props.selectedValue : _value === 'すべて');
      
      // return props.selectedValue ? _value === props.selectedValue : _value === 'すべて'
    }

    const style = {
      '--width': props.width,
    }

    // onMounted(async () => {
    //   console.log('aaa');
    //   console.log(root.value.querySelector('.iscroll-wrapper'));

    //   // state.iScrollObj = new IScroll(root.value.querySelector('.iscroll-wrapper2'), {
    //   //   scrollX: false,
    //   //   scrollY: true,
    //   //   disableTouch: true,
    //   //   disablePointer: false,
    //   //   useTransition: false,
    //   //   scrollbars: 'custom',
    //   //   fadeScrollbars: true,
    //   // })
    // })

    onUpdated(() => {
      if (root.value.querySelector('.iscroll-wrapper')) {
        state.iScrollObj = new IScroll(root.value.querySelector('.iscroll-wrapper'), {
          scrollX: false,
          scrollY: true,
          disableTouch: true,
          disablePointer: false,
          useTransition: false,
          scrollbars: 'custom',
          fadeScrollbars: true,
          mouseWheel: true,
        })
      }

      setTimeout(() => {
        if (state.iScrollObj) {
          state.iScrollObj.refresh()
        }
      }, 300)
    })

    return {
      root,
      state,
      style,
      onTapItem,
      changeSelectBoxDisplay,
      onTapOutside,
      isSelected,
    }
  },
})
</script>

