  width: var(--width);
  height: 30px;
  background-color: #fff;
  border-radius: 3px;
  margin-right: 3.5px;
  margin-bottom: 3px;

  &__ {
    &value {
      position: relative;
      width: 100%;
      min-height: 27px;
      line-height: 27px;
      font-size: 1.1rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    &arrow {
      width: 8px;
      height: 8px;
      margin-top: -4px;
      border-bottom: 2px solid #37424a;
      border-right: 2px solid #37424a;
      opacity: 0.5;
      transform: rotate(45deg);
      transition: transform 0.15s linear;

      &--on {
        transform: rotate(-135deg) translate(-2px, -2px);
      }
    }

    &items {
      padding: 10px 0;
      position: absolute;
      left: 0;
      top: 34px;
      width: 100%;
      min-height: 0;
      background: $background;
      overflow: hidden;
      box-shadow: 0px 2px 20px #00000029;
      transform: translate3d(0, 0, 0);
      z-index: 1;
    }
    &item {
      position: relative;
      padding: 0 30px 0 10px;
      width: 100%;
      line-height: 1.4;
      margin-bottom: 10px;
      font-size: 13px;
      overflow: hidden;
      height: 34px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      &--on {
        background: #30a6a71f;

        &:before {
          content: '';
          position: absolute;
          width: 10px;
          height: 6px;
          top: 13px;
